import  React, {useState, useEffect} from 'react';
import articleContent from './article-content'
import ArticlesList from "../component/ArticlesList";
import NotFoundPage from "./NotFoundPage";
import {fetch} from "whatwg-fetch";
import CommentsList from "../component/CommentsList";
import UpvotesSection from "../component/UpvotesSection";
import AddCommentForm from "../component/AddCommentForm";
const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article  => article.name === name)
    const [articleInfo, setArticleInfo] = useState({upvotes: 0, comments: []});
    useEffect(() =>{
        const fetchData = async () =>{
            const result = await fetch(`http://localhost:8000/api/articles/${name}`);
            const body = await result.json();
            console.log(body);
            setArticleInfo(body);
        }
        fetchData();
    }, [name])
    if(!article) return <NotFoundPage/>
    const otherArticles  = articleContent.filter(articles => articles.name !== name);
    return (
        <React.Fragment>
            <h1>{article.title}</h1>
            <UpvotesSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo}></UpvotesSection>
            {article.content.map((paragraph, key) =>(
                <p key={key}>{paragraph}</p>
            ))}
            <CommentsList comments={articleInfo.comments} />
            <AddCommentForm articleName={name} setArticlesInfo={setArticleInfo}/>
            <h3>Other Articles: </h3>
            <ArticlesList articles={otherArticles}/>
        </React.Fragment>
    )


};

export default ArticlePage;
